* {
  margin: 0;
  padding: 0;
}
.container-nav {
  display: grid;
  grid-template-rows: repeat(5, 1fr);
  grid-row: 1/7;
  background: $blacksecondary;
  -webkit-box-shadow: 6px 6px 7px $neumorphimsprimary,
    -6px -6px 7px $neumorphimssecondary;
  box-shadow: 6px 6px 7px $neumorphimsprimary,
    -6px -6px 7px $neumorphimssecondary;
  position: fixed;
  top: 0;
}
nav {
  display: flex;
  flex-direction: column;
  width: $nav;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
  ul:nth-child(2) {
    margin-left: 121px;
  }
}

//Start hover effect RRSS
ul li {
  list-style: none;
}
ul li a {
  position: relative;
  display: block;
  width: 50px;
  height: 50px;
  text-align: center;
  background: $blacksecondary;
  border-radius: 50%;
  transition: 0.5s;
}
ul li a::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: $whitesecondary;
  transition: 0.5s;
  transform: scale(0.9);
  z-index: -1;
}
ul li a:hover::before {
  transform: scale(1.1);
  box-shadow: 0 0 15px $whitesecondary;
}
ul li a:hover {
  color: $white;
  box-shadow: 0 0 5px $whitesecondary;
}
//End hover effect RRSS

.container-img-p-navmenu .menu nav{
  display: flex;
  align-items: center;
  text-align: left;
  margin-left: 20px;
}
.rightarrow {
  width: 60%;
  position: absolute;
  margin-bottom: 14px;
}
.home {

  transition: all 300ms linear ;
  &:hover {
    padding-left: 90px;
    
  }

}
.social a,
.menu p {
  display: block;
  color: $white;
  text-decoration: none;
  font-size: $fontmenunav;
  font-weight: $fontweightnav;
  margin-bottom: 14px;
  cursor: pointer;
}
.social li:last-child {
  margin-bottom: 100px;
}
.logo-img {
  width: 35px;
}
